<script setup>
import {ref, computed, onMounted} from 'vue';
import {useRouter} from 'vue-router';
import {events} from '@/assets/event';
import {users} from '@/assets/user';

const router = useRouter();

// Â∏∏Èáè
const MAX_DICE = 6;
const MAX_HEALTH = 3;

// ÂìçÂ∫îÂºèÊï∞ÊçÆ
const logMessages = ref([]);
const user = ref(JSON.parse(localStorage.getItem('user') || '{}'));
const health = ref(3);
const duelState = ref(false);
const gameOver = ref(false);
const newUsers = users.filter(u => u.id !== user.value.id);
const newEvents = events.filter(e => e.rela !== user.value.id);
const pkUser = ref(null);
const pkUserRoll = ref(0);
const userRoll = ref(0);
const eventNow = ref({});

// Êñ∞Â¢ûÔºö‰∫ã‰ª∂ËÆ∞ÂΩïÊï∞ÁªÑ
const eventHistory = ref([]);
const round = ref(0); // ËΩÆÊ¨°

// Ê∏∏ÊàèÊÄªÁªìÂØπËØùÊ°Ü
const summaryVisible = ref(false);
const summaryStats = ref({
  totalRounds: 0,
  totalEvents: 0,
  totalDuels: 0,
  wins: 0,
  losses: 0,
  draws: 0,
  finalHealth: 0,
});

// ËÆ°ÁÆóÂ±ûÊÄßÔºöË°ÄÈáèÁÇπÊï∞ÁªÑ
const healthDots = computed(() => Array.from({length: health.value}));

// ÂÖ¨ÂÖ±ÊñπÊ≥ïÔºöÊé®ÈÄÅÊó•Âøó
const pushLog = (msg) => {
  const newLog = `[${new Date().toLocaleTimeString()}] ${msg}`;
  logMessages.value.unshift(newLog);
};

// ÊñπÊ≥ï
const restartGame = () => {
  localStorage.removeItem('user');
  router.push('/');
};

const singlePull = () => {
  if (gameOver.value) return;
  round.value++;
  eventNow.value = newEvents[Math.floor(Math.random() * newEvents.length)];
  if (eventNow.value.roll!=null) {
    pkUserRoll.value = 0;
    userRoll.value = 0;
    const randomUser = newUsers[Math.floor(Math.random() * newUsers.length)];
    pkUser.value  = randomUser;
    let output = eventNow.value.content.replace('{name}', randomUser.name);
    pushLog(output);

    userRoll.value = Math.floor(Math.random() * 6) + 1;
    pushLog(`${randomUser.name} Êé∑Âá∫‰∫Ü ${userRoll.value},ËΩÆÂà∞‰Ω†‰∫Ü`);
    duelState.value = true;

    // ËÆ∞ÂΩï‰∫ã‰ª∂
    eventHistory.value.push({
      round: round.value,
      type: 'duel',
      opponent: randomUser.name,
      opponentRoll: userRoll.value,
      result: 'pending'
    });
  } else {
    health.value = Math.max(0, Math.min(MAX_HEALTH, health.value + eventNow.value.type));
    pushLog(eventNow.value.content);

    // ËÆ∞ÂΩï‰∫ã‰ª∂
    eventHistory.value.push({
      round: round.value,
      type: 'event',
      content: eventNow.value.content,
      health: health.value
    });
    checkGameOver();
  }
};

const rollTheDice = () => {
  if (gameOver.value) return;
  const diceValue = Math.floor(Math.random() * MAX_DICE) + 1;
  pushLog(`ÊäïÊé∑Âá∫‰∫Ü: ${diceValue}`);

  let result;
  if (diceValue > userRoll.value) {
    result = 'win';
    pushLog('Ëá™Âè§ÂØπÊ≥¢Â∑¶ËæπËµ¢ÂêóÔºüÊÅ≠Âñú‰Ω†');
  } else if (diceValue === userRoll.value) {
    result = 'draw';
    pushLog('Á´üÁÑ∂Âπ≥Â±Ä‰∫ÜÂêóÔºåÂìàÂü∫‰∫∫Ôºå‰Ω†ËøôÂÆ∂‰ºô');
  } else {
    result = 'lose';
    health.value--;
    pushLog('‰Ω†ËæìÁªô‰∫Ü'+pkUser.value.name);
  }

  // Êõ¥Êñ∞ÊúÄÂêé‰∏ÄÊ¨°ÂØπÊàòÁöÑÁªìÊûú
  const lastDuel = eventHistory.value.findLast(e => e.type === 'duel' && e.result === 'pending');
  if (lastDuel) {
    lastDuel.playerRoll = diceValue;
    lastDuel.result = result;
    lastDuel.health = health.value;
  }

  duelState.value = false;
  checkGameOver();
};

// Âà§Êñ≠Ê∏∏ÊàèÊòØÂê¶ÁªìÊùü
const checkGameOver = () => {
  if (health.value <= 0) {
    health.value = 0;
    gameOver.value = true;
    pushLog('üíÄ Ê∏∏ÊàèÁªìÊùüÔºÅËØ∑ÁÇπÂáªÈáçÂºÄÊåâÈíÆÈáçÊñ∞ÂºÄÂßã„ÄÇ');
    generateSummary();
  }
};

// ÁîüÊàêÊÄªÁªìÊï∞ÊçÆ
const generateSummary = () => {
  const duels = eventHistory.value.filter(e => e.type === 'duel');
  const wins = duels.filter(d => d.result === 'win').length;
  const losses = duels.filter(d => d.result === 'lose').length;
  const draws = duels.filter(d => d.result === 'draw').length;

  summaryStats.value = {
    totalRounds: round.value,
    totalEvents: eventHistory.value.filter(e => e.type === 'event').length,
    totalDuels: duels.length,
    wins,
    losses,
    draws,
    finalHealth: health.value,
  };

  summaryVisible.value = true;
};

// ÂàùÂßãÂåñ
onMounted(() => {
  if (user.value?.tips) {
    pushLog(user.value.tips);
  }
});
</script>

<template>
  <div class="game-container">
    <!-- È°∂ÈÉ®‰ø°ÊÅØÊ†è -->
    <div class="top-panel">
      <div class="avatar-placeholder">
        <img v-if="user?.url" :src="user.url" class="avatar"/>
      </div>
      <div class="info-area">
        <div class="player-name">{{ user?.name || 'Êú™Áü•Áé©ÂÆ∂' }}</div>
        <div class="health-bar">
          <img
              v-for="(dot, idx) in healthDots"
              :key="idx"
              src="@/assets/health-dot.png"
              class="health-dot"
          />
        </div>
      </div>
      <el-button type="danger" plain @click="restartGame">ÈáçÂºÄ</el-button>
    </div>

    <!-- Êó•ÂøóÈù¢Êùø -->
    <div class="log-panel" ref="logPanel">
      <div v-if="logMessages.length === 0" class="log-placeholder">ÊöÇÊó†Êó•Âøó</div>
      <p v-else v-for="(msg, index) in logMessages" :key="index">{{ msg }}</p>
    </div>

    <!-- Êìç‰ΩúÈù¢Êùø -->
    <div class="action-panel">
      <el-button :disabled="duelState || gameOver" type="danger" plain @click="singlePull">ÊäΩÂèñ</el-button>
      <el-button :disabled="!duelState || gameOver" type="danger" plain @click="rollTheDice">ÊäïÊé∑È™∞Â≠ê</el-button>
    </div>
    <div class="bottom-panel" v-if="duelState">
      <div class="avatar-placeholder">
        <img v-if="user?.url" :src="user.url" class="avatar"/>
      </div>
      <div class="avatar-placeholder">
        <img src="@/assets/vs.png" class="avatar"/>
      </div>
      <div class="avatar-placeholder">
        <img v-if="user?.url" :src="pkUser.url" class="avatar"/>
      </div>
    </div>
<!--    <div class="bottom-panel" v-if="eventNow.img!=null">-->
<!--      <img :src="eventNow.img" class="event-img" alt=""/>-->
<!--    </div>-->

    <!-- Ê∏∏ÊàèÊÄªÁªìÂºπÁ™ó -->
    <el-dialog v-model="summaryVisible" title="Ê∏∏ÊàèÊÄªÁªì" width="400px" align-center>
      <div class="summary-content">
        <p>ÊÄªËΩÆÊ¨°Ôºö{{ summaryStats.totalRounds }}</p>
        <p>‰∫ã‰ª∂Êï∞Ôºö{{ summaryStats.totalEvents }}</p>
        <p>ÂØπÊàòÊ¨°Êï∞Ôºö{{ summaryStats.totalDuels }}</p>
        <p>ËÉúÂà©Ôºö{{ summaryStats.wins }} | Â§±Ë¥•Ôºö{{ summaryStats.losses }} | Âπ≥Â±ÄÔºö{{ summaryStats.draws }}</p>
        <p>ÊúÄÁªàË°ÄÈáèÔºö{{ summaryStats.finalHealth }}</p>
      </div>
      <template #footer>
        <el-button type="primary" @click="summaryVisible=false">Á°ÆÂÆö</el-button>
      </template>
    </el-dialog>
  </div>
</template>

<style scoped>
/* È¢úËâ≤ÂèòÈáè */
:root {
  --main-border-color: #f56c6c;
  --main-text-color: #f56c6c;
  --background-color: #f0f2f5;
  --health-filled-color: #f56c6c;
}

.game-container {
  width: 100%;
  height: 70vh;
  background-color: var(--background-color);
  padding: 15px;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  font-family: 'Helvetica Neue', Helvetica, 'PingFang SC', 'Hiragino Sans GB',
  'Microsoft YaHei', 'ÂæÆËΩØÈõÖÈªë', Arial, sans-serif;
  color: var(--main-text-color);
}

/* È°∂ÈÉ®Èù¢Êùø */
.top-panel {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 3vh;
}
.bottom-panel{
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: absolute;
  bottom: 10vh;
  width: 86%;
  padding-left: 10px;
  padding-right: 10px;
}


.avatar-placeholder {
  width: 80px;
  height: 80px;
  border: 2px solid var(--main-border-color);
  display: flex;
  justify-content: center;
  align-items: center;
}

.info-area {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}

.player-name {
  font-size: 20px;
  font-weight: bold;
}

.health-bar {
  display: flex;
  align-items: center;
}

.health-dot {
  width: 18px;
  height: 18px;
  margin: 0 4px;
}

/* Êó•ÂøóÈù¢Êùø */
.log-panel {
  flex-grow: 1;
  border: 2px solid var(--main-border-color);
  padding: 10px;
  overflow-y: auto;
  margin-bottom: 15px;
  background-color: white;
}

.log-placeholder {
  font-size: 16px;
  color: #aaa;
}

.log-panel p {
  margin: 0 0 5px 0;
  font-size: 14px;
}

/* Êìç‰ΩúÈù¢Êùø */
.action-panel {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

/* Element Plus ÊåâÈíÆ */
.el-button {
  width: 120px;
  height: 45px;
  font-size: 18px;
  border-width: 2px;
}

.el-button.is-plain {
  color: var(--main-text-color);
  border-color: var(--main-border-color);
  background-color: transparent;
}

.el-button.is-plain:hover,
.el-button.is-plain:focus {
  background-color: #fef0f0;
  color: var(--main-text-color);
  border-color: var(--main-border-color);
}

.avatar {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.summary-content {
  font-size: 16px;
  line-height: 1.8;
}
.event-img{
  position: fixed;
  bottom: 0;
}
</style>
